import React from 'react';
import Moment from 'react-moment';

export const About = ({ location, twitter, update, created }) => {
  return (
    <div className='about container'>
      {/* <h1>
        joined <Moment date={created} format='DD MMM Y' />{' '}
      </h1>
      <h1>
        last update <Moment date={update} format='DD MMM Y' />{' '}
      </h1> */}
      {/* location */}
      <div className='desc'>
        <span>
          <i className='fas fa-map-marker-alt fa-2x' aria-hidden='true'></i>
        </span>
        <div className='location'>{location ? location : 'Not Available'}</div>
      </div>
      {/* twitter */}
      <div className='desc'>
        <span>
          <i className='fab fa-twitter fa-2x' aria-hidden='true'></i>
        </span>
        <div className='twitter'>{twitter ? twitter : 'Not Available'}</div>
      </div>
      {/* register date */}
      <div className='desc'>
        <span>
          <i className='far fa-calendar-alt fa-2x' aria-hidden='true'></i>
        </span>
        <div className='created'>
          <p>
            <Moment date={created} format='DD MMM Y' />
          </p>
        </div>
      </div>
    </div>
  );
};
